import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";

export default {
  name: "fish_statistic",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    return {
      pagesize: 10,
      currentPage: 1,
      total: 0,
      dialogFormVisible: false,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      format: {
        play_id: "",
        // play_counter: "",
        dateArr: [
          new Date(new Date().getTime() - 3600 * 1000 * 24 * 7),
          new Date()
        ]
      },
      tableStyle: [
        { label: "玩家id", prop: "uid", width: "" },
        { label: "子弹发送总量", prop: "total_bullet", width: "" },
        { label: "子弹价值", prop: "bullet_value", width: "" },
        { label: "鱼总价值", prop: "fish_value", width: "" },
        { label: "收支", prop: "payment", width: "" },
        { label: "操作", prop: "action", width: "150" }
      ],
      records: [],
      recordTableData: [],
      pageInfo: new PageInfo(0, [5, 10, 15], 5)
    };
  },
  methods: {
    getFishList() {
      this.$http.get("v1/backend/operation/fishes").then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.records = res.data.data;
          this.total = res.data.total;
        }
      });
    },
    /**搜索*/
    searchData() {
      let params = {
        user_id: Number(this.format.play_id),
        start_time: this.format.dateArr
          ? parseInt(new Date(Number(this.format.dateArr[0])).getTime() / 1000)
          : 0,
        end_time: this.format.dateArr
          ? parseInt(new Date(Number(this.format.dateArr[1])).getTime() / 1000)
          : 0
      };
      this.$http
        .get("v1/backend/operation/fishes", {
          params
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 200) {
            this.records = res.data.data;
            this.total = res.data.total;
          }
        });
    },
    handleRecord(row) {
      console.log(row);
      this.dialogFormVisible = true;
      let params = {
        user_id: Number(row.uid),
        start_time: this.format.dateArr
          ? parseInt(new Date(Number(this.format.dateArr[0])).getTime() / 1000)
          : 0,
        end_time: this.format.dateArr
          ? parseInt(new Date(Number(this.format.dateArr[1])).getTime() / 1000)
          : 0
      };
      this.$http
        .get("v1/backend/operation/fish-data", {
          params
        })
        .then(res => {
          console.log(res);
          if(res.data.code === 200) {
            this.recordTableData = res.data.data
          }
        });
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.getFishList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getFishList();
    }
  },
  mounted() {
    this.getFishList();
  }
};



